import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { BsChevronDoubleDown } from "react-icons/bs";
import { Container, Row, Col, Button } from "react-bootstrap";

import paperData from "./paperData";

class Publications extends Component {
  render() {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <Topic />
        <PublicSection />
      </React.StrictMode>
    );
  }
}

export { Publications };


function Topic() {
  return (
    <div className="topicSection">
      <div className="title">
        <p>Publications</p>
      </div>
    </div>
  );
}

function SelectBtn(props) {
  return (
    <Container>
      <Row>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => { props.selectChange('papers') }}
          >
            <p>期刊論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button variant="outline-dark" className="w-100" onClick={() => { props.selectChange('proceedings') }}>
            <p>會議論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button variant="outline-dark" className="w-100" onClick={() => { props.selectChange('bookPaper') }}>
            <p>專書論文</p>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

function PublicationsList(props) {
  var publicationsList
  if (props.type == 'bookPaper') {
    publicationsList = paperData.bookPaper
  } else if (props.type == 'proceedings') {
    publicationsList = paperData.proceedings
  } else if (props.type == 'papers') {
    publicationsList = paperData.papers
  }


  return (
    <Container>
      <Row>
        {
          publicationsList.map((p) => (
            <Col xs={12} >
              <PublicationsCard
                link={p.link}
                title={p.title}
                author={p.author}
                at={p.at}
              />
            </Col>
          ))
        }
      </Row>
    </Container>
  )


}

function PublicationsCard(props) {
  return (
    <React.StrictMode>
      <a href={'https://scholar.google.com/scholar?hl=zh-TW&q=' + props.title} target='_blank'
         style={{textDecoration:'none'}}>
        <div className="publications-card">
          <div className="title">
            <p className='m-0'>{props.title}</p>
          </div>
          <div className="at">
            <p className='m-0'>{props.at}</p>
          </div>
          <div className="author">
            <p className='m-0'>{props.author}</p>
          </div>
        </div>
      </a>
      <hr />
    </React.StrictMode>

  );
}

function PublicSection() {
  const [paperType, setPapreType] = useState("papers");

  useEffect(() => {
    console.log(paperType)
  }, [paperType])


  return (
    <React.StrictMode>
      <SelectBtn
        selectChange={(type) => {
          setPapreType(type)
        }}
      />
      <PublicationsList type={paperType} />

    </React.StrictMode>
  );
}
